import React, { useEffect } from "react";
import "../css/style.css";
import pic3 from "../Image/mypicture3.png";
import background from '../Image/peaceful.jpg'
import { Link } from "react-router-dom";
import ChatBot from "./ChatBot"

function HomePage() {

  useEffect(() => {
    document.body.style.backgroundImage = `url(${background})`;
    document.body.style.backgroundRepeat = "no-repeat";
  });

  return (
    <div id="container">
       <React.Fragment>
        <ChatBot />
      </React.Fragment>
    
      <div className="box">
        <img src={pic3} alt="thisismypicture" className="box-img"></img>
        <h1>Ky Huu Dong</h1>
        <h3>Da Nang City,Viet Nam</h3>
        <h3>IT Major | Duy Tan University's student</h3>
        <h5>
          Handsome, talented, top thinking, leadership, and many other qualities
          are what i have üòù.
        </h5>
        <ul>
          <li>
            <a href="https://www.facebook.com/huu.dong.1" target="_blank">
              <i className="fa fa-facebook-square fa-3x"></i>acebook
            </a>
          </li>
          <li>
            <a href="https://www.instagram.com/dongghuuu/" target="_blank">
              <i className="fa fa-instagram fa-3x"></i>Instagram
            </a>
          </li>
        </ul>
        <Link to="/react-deploy/updating">
          <li>
            <a className="update">
              <i className="fa fa-chevron-right fa-2x"></i>
            </a>
          </li>
        </Link>
      </div>
    </div>
  );
}

export default HomePage;
