import React, { useEffect } from 'react';
import '../css/u-style.css';
import picDog from '../Image/dogImage.jpg';
import background from '../Image/background.jpg';
import {Link} from  'react-router-dom';

function Updating() {

    useEffect(() => {
            document.body.style.backgroundImage = `url(${background})`;
            document.body.style.backgroundRepeat = "no-repeat";
    }, [])


        return(
            <div className="u-box">
                <img src={picDog} alt="dogimage" className="u-box-img"></img>
                <h2>This feature i am updating üçî</h2>

                <Link to='/react-deploy/'>
                    <a><i className="fa fa-chevron-left fa-3x"></i></a>
                 </Link>
            </div>
        )
}

export default Updating;